@use "global" as *;

.contact__message {
    font-size: rem(14);
    margin-top: rem(60);
    line-height: 1.714;

    @include mq(md) {
        margin: rem(80) auto;
        width: 100%;
        max-width: rem(728);
    }
}

.contact__message span {
    color: #EE5A6D;
}

.contact__wrapper {
    margin-top: rem(100);

    @include mq(md) {
        margin: rem(160) auto;
        width: 100%;
        max-width: rem(728);
    }
}

.contact-header {
    position: relative;
    text-align: center;
}

.contact-header__title {
        font-size: rem(20);
        font-weight: $bold;
        display: inline-block;
        letter-spacing: 0.08em;

        @include mq(md) {
            font-size: rem(28);
        }

        &::before {
            position: absolute;
            content: "";
            width: rem(48);
            height: rem(12);
            top: 50%;
            left: rem(225);
            transform: translateY(-50%);
            background: url(../src/images/common/section-title.deco.svg);

            @include mq(md) {
                left: rem(117);
                width: rem(67);
                height: rem(20);
                background: url(../src/images/top/title-deco.svg);
            }
        }
        &::after {
            position: absolute;
            content: "";
            width: rem(48);
            height: rem(12);
            top: 50%;
            right: rem(225);
            transform: translateY(-50%);
            background: url(../src/images/common/section-title.deco.svg);

            @include mq(md) {
                right: rem(117);
                width: rem(67);
                height: rem(20);
                background: url(../src/images/top/title-deco.svg);
            }
        }
    }


br {
    @include mq(md) {
        display: none;
    }
}

.contact-form {
    margin-top: rem(68);

    @include mq(md) {
        margin-top: rem(72);
    }
}

.contact-form__row {
    border-top: rem(1) solid #DDDDDD;
    padding-top: rem(28);
    padding-bottom: rem(28);

    @include mq(md) {
        display: flex;
        align-items: center;
        padding-top: rem(16);
        padding-bottom: rem(25);
    }
}

.contact-form__label {

    @include mq(md) {
        width: 100%;
        max-width: rem(240);
    }
}

.contact-form__label p{
    font-size: rem(11);
    color: #888888;
}

.contact-form__label label {
    position: relative;
    display: inline;
    padding-right: rem(48);

    &::before {
        position: absolute;
        content: "必須";
        padding: rem(3) rem(6);
        background-color: #EE5A6D;
        color: $white;
        right: 0;
        border-radius: rem(4);
        font-size: rem(12);
    }
}

.contact-form__input {
    margin-top: rem(12);

    @include mq(md) {
        width: calc(100% - rem(240));
    }

    [type="text"],
    [type="email"],
    [type="date"] ,
    [type="tel"] {
        background: #F6F6F6;
        padding: rem(13) rem(16);
        width: 100%;
    }
}

.contact-form__radio-body {
    margin-top: rem(20);

    @include mq(md) {
        margin: 0;
    }
}

.contact-form__radio span {
    font-size: rem(14);
    padding-left: rem(32);
    margin-right: rem(40);
    position: relative;
    display: inline-block;

    &::before {
        content: "";
        position: absolute;
        width: rem(24);
        height: rem(24);
        border: rem(1) solid $main-color;
        border-radius: 50%;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        background-color: #fff;
    }

    &::after {
        content: "";
        position: absolute;
        width: rem(12);
        height: rem(12);
        border: rem(1) solid $main-color;
        border-radius: 50%;
        left: rem(6);
        top: 50%;
        transform: translateY(-50%);
        background-color: $main-color;
        display: none;
    }
}

.contact-form__radio {
    border-top: rem(1) solid #DDDDDD;
    padding-top: rem(28);
    padding-bottom: rem(28);

    [type="radio"]:checked + span {
        &::after {
            display: block;
        }
    }

    @include mq(md) {
        display: flex;
        align-items: center;
        padding-top: rem(16);
        padding-bottom: rem(16);
    }
}

.contact-form__check {
    border-top: rem(1) solid #DDDDDD;
    padding-top: rem(28);
    padding-bottom: rem(28);

    @include mq(md) {
        display: flex;
        align-items: center;
    }
}

.wpcf7-list-item.first {
    margin: 0;
}
.wpcf7-list-item-label {
    cursor: pointer;
    display: flex;
}
  /* デフォルトのボタン */
input[type="radio"] {
    opacity: 0; // デフォルトのボタンを非表示
    position: absolute;
}
  /* チェック前のボタン */
.wpcf7-list-item-label::before {
    background: #fff;
    border: 1px solid #333;
    border-radius: 100%; // ラジオボタンっぽく丸くする
    content: "";
    height: 1.2em;
    margin-bottom: auto;
    margin-right: .5em;
    margin-top: auto;
    transition: background-color .5s; // 色をじんわり変化させる
    width: 1.2em;
}
  /* チェック後のボタン */
input[type="radio"]:checked + .wpcf7-list-item-label::before {
    background-color: #7fbfff; // チェック後の中心の色
    box-shadow: inset 0 0 0 3px #fff; // 中心の色のスタイル
}
