.contact-form__select{
        padding: rem(13) rem(16);
        margin-top: rem(12);
        background-image: url(../src/images/contact/select-arrow.png);
        background-position: center right rem(20);
        background-repeat: no-repeat;
        background-size: rem(20) rem(20);
        background-color: #F6F6F6;

        @include mq(md) {
            width: calc(100% - rem(240));
        }

        select {
            width: 100%;
        }
}

.contact-form__text {
    margin-top: rem(12);

    @include mq(md) {
        width: calc(100% - rem(240));
    }

    textarea {
        background: #F6F6F6;
        padding: rem(13) rem(16);
        width: 100%;
        height: rem(200);
        resize: none;
    }
}

.contact-form__btn {
    padding-top: rem(68);
    border-top: rem(1) solid #DDDDDD;
    text-align: center;

    [type="submit"] {
        padding: rem(9) rem(113);
        color: $main-color;
        background-color: $white;
        border: rem(1) solid $main-color;
        border-radius: rem(24);
    }
}